//------------------------------------------------------------------------------
// SHADOWS
//------------------------------------------------------------------------------

// Focus shadow mixin.
@mixin focus-shadow() {
    box-shadow: 0 0 8px rgba(0,0,0,.18),0 8px 16px rgba(0,0,0,.36);
}

@mixin shadow-0dp() {
    box-shadow: none;
}
@mixin shadow-1dp() {
    box-shadow: 0 2px 1px -1px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 1px 1px 0 rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 1px 3px 0 rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-2dp() {
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 2px 2px 0 rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 1px 5px 0 rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-3dp() {
    box-shadow: 0 3px 3px -2px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 3px 4px 0 rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 1px 8px 0 rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-4dp() {
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 4px 5px 0 rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 1px 10 0 rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-5dp() {
    box-shadow: 0 3px 5px -1px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 5px 8px 0 rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 1px 14px 0 rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-6dp() {
    box-shadow: 0 3px 5px -1px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 6px 10 0 rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 1px 18px 0 rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-7dp() {
    box-shadow: 0 4px 5px -2px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 7px 10 1px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 2px 16px 1px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-8dp() {
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 8px 10 1px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 3px 14px 2px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-9dp() {
    box-shadow: 0 5px 6px -3px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 9px 12px 1px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 3px 16px 2px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-10dp() {
    box-shadow: 0 6px 6px -3px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 10 14px 1px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 4px 18px 3px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-11dp() {
    box-shadow: 0 6px 7px -4px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 11px 15px 1px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 4px 20 3px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-12dp() {
    box-shadow: 0 7px 8px -4px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 12px 17px 2px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 5px 22px 4px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-13dp() {
    box-shadow: 0 7px 8px -4px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 13px 19px 2px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 5px 24px 4px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-14dp() {
    box-shadow: 0 7px 9px -4px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 14px 21px 2px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 5px 26px 4px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-15dp() {
    box-shadow: 0 8px 9px -5px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 15px 22px 2px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 6px 28px 5px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-16dp() {
    box-shadow: 0 8px 10 -5px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 16px 24px 2px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 6px 30 5px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-17dp() {
    box-shadow: 0 8px 11px -5px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 17px 26px 2px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 6px 32px 5px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-18dp() {
    box-shadow: 0 9px 11px -5px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 18px 28px 2px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 7px 34px 6px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-19dp() {
    box-shadow: 0 9px 12px -6px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 19px 29px 2px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 7px 36px 6px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-20dp() {
    box-shadow: 0 10 13px -6px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 20 31px 3px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 8px 38px 7px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-21dp() {
    box-shadow: 0 10 13px -6px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 21px 33px 3px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 8px 40 7px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-22dp() {
    box-shadow: 0 10 14px -6px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 22px 35px 3px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 8px 42px 7px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-23dp() {
    box-shadow: 0 11px 14px -7px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 23px 36px 3px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 9px 44px 8px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
@mixin shadow-24dp() {
    box-shadow: 0 11px 15px -7px rgba(0, 0, 0, $shadow-key-umbra-opacity),
                0 24px 38px 3px rgba(0, 0, 0, $shadow-key-penumbra-opacity),
                0 9px 46px 8px rgba(0, 0, 0, $shadow-ambient-shadow-opacity);
}
